// Takes an ISO time and returns a string representing how
// long ago the date represents.
var monthArray = ['January', 'February', 'March', 'April', 'May', 'June', 'July',
        'August', 'September', 'October', 'November', 'December']

function prettyDate(time){
	var date = new Date(time),
		diff = (((new Date()).getTime() - date.getTime()) / 1000),
		day_diff = Math.floor(diff / 86400);
			
	if ( isNaN(day_diff) || day_diff < 0)
		return;

    if (day_diff < 7) {
        return day_diff == 0 && (
            diff < 60 && "just now" ||
            diff < 120 && "1 minute ago" ||
            diff < 3600 && Math.floor( diff / 60 ) + " minutes ago" ||
            diff < 7200 && "1 hour ago" ||
            diff < 86400 && Math.floor( diff / 3600 ) + " hours ago") ||
            day_diff == 1 && "Yesterday" ||
            day_diff < 7 && day_diff + " days ago";
    } else {
        return simpleDateFormatter(time)
    }
}
function simpleDateFormatter(time) {
    let date = new Date(time)
    return `${monthArray[date.getMonth()]} ${date.getDate()}, ${date.getFullYear()}`
}
export {prettyDate, simpleDateFormatter}



